enum Language {
  en = 'en',
  zh = 'zh'
}

export function translate(language: string | Language): string {
  switch (language) {
    case Language.en:
      return 'English';
    case Language.zh:
      return '';
    default:
      return '';
  }
}

export function toLanguage(language: string): Language {
  switch (language) {
    case 'English':
      return Language.en;
    case '':
      return Language.zh;
    default:
      return Language.zh;
  }
}

export default Language;
