


























































import UserApi from '@/apis/UserApi';
import Vue from 'vue';
import liff from '@line/liff';
import i18n from '@/i18n';
import Language, { toLanguage, translate } from '@/models/LanguageModel';

export default Vue.extend({
  components: {
  },
  data: () => ({
    valid: false,
    userName: '',
    businessDefinedId: '',
    userNameRules: [
      (v: string) => !!v || i18n.t(''),
      (v: string) => v.length <= 10 || i18n.t('10'),
    ],
    businessDefinedIdRules: [
      (v: string) => v.length <= 20 || i18n.t('20'),
    ],
    language: translate(Language.zh),
  }),
  computed: {
    availableLanguages(): string[] {
      const result = [] as string[];
      Object.keys(Language).forEach((language) => {
        result.push(translate(language));
      });
      return result;
    },
  },
  async mounted() {
    try {
      const intervalId = setInterval(() => {
        if (this.$store.state.userId) {
          clearInterval(intervalId);
          this.loadProfile();
        }
      }, 100);
    } catch (error) {
      console.error(error);
    }
  },
  methods: {
    async loadProfile() {
      const profile = await UserApi.retrieveProfile();
      if (profile.name) {
        this.userName = profile.name;
      }

      if (profile.businessDefinedId) {
        this.businessDefinedId = profile.businessDefinedId;
      }

      if (profile.language) {
        this.language = translate(profile.language);
      }
    },
    async submit() {
      console.log(this.userName, this.businessDefinedId);
      try {
        await UserApi.updateProfile(this.userName, this.businessDefinedId, toLanguage(this.language));

        if (!liff.isInClient()) {
          this.$notify({
            type: 'success',
            title: this.$t('').toString(),
            text: this.$t('').toString(),
          });
        } else {
          liff.sendMessages([{
            type: 'text',
            text: this.$t('').toString(),
          }]);
          liff.closeWindow();
        }
      } catch (error) {
        this.handleError(error);
      }
    },

    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    handleError(error: any) {
      this.$notify({
        type: 'error',
        title: this.$t('').toString(),
        text: this.$t('').toString(),
      });
      throw error;
    },
  },
});
